 is paused! Please contact Administrator\n            </div>\n            <div v-if=\"currentdetails?.status == 'paused'\" class=\"Paused\">\n                Your subscription is paused! Please contact Administrator\n            </div>\n            <div class=\"info\">\n                <div v-if=\"currentdetails?.razorpay_success?.remaining_count\">\n                    Remaining Payment Count:\n                    {{ currentdetails?.razorpay_success?.remaining_count }}\n                </div>\n                <div v-if=\"currentdetails?.razorpay_success?.charge_at\">\n                    Next Payment:\n                    {{\n                        new Date(\n                            currentdetails?.razorpay_success?.charge_at * 1000\n                        ).toLocaleString()\n                    }}\n                </div>\n                <div v-if=\"currentdetails?.razorpay_success?.current_start\">\n                    Current Billing started on:\n                    {{\n                        new Date(\n                            currentdetails?.razorpay_success?.current_start *\n                                1000\n                        ).toLocaleString()\n                    }}\n                </div>\n                <div v-if=\"currentdetails?.razorpay_success?.current_end\">\n                    Current Billing will end on:\n                    {{\n                        new Date(\n                            currentdetails?.razorpay_success?.current_end * 1000\n                        ).toLocaleString()\n                    }}\n                </div>\n                <div v-if=\"currentdetails?.razorpay_success?.start_at\">\n                    Subscription started on:\n                    {{\n                        new Date(\n                            currentdetails?.razorpay_success?.start_at * 1000\n                        ).toLocaleString()\n                    }}\n                </div>\n                <div v-if=\"currentdetails?.razorpay_success?.end_at\">\n                    Subscription will end on:\n                    {{\n                        new Date(\n                            currentdetails?.razorpay_success?.end_at * 1000\n                        ).toLocaleString()\n                    }}\n                </div>\n            </div>\n            <div style=\"margin: auto; overflow-x: auto\">\n                <div class=\"price-card-container\">\n                    <div>\n                        <div :style=\"current.color\" class=\"plan\">\n                            {{ current.name }}\n                        </div>\n                    </div>\n                    <div class=\"details-cont\">\n                        <div>/Per Month</div>\n                        <div style=\"text-align: center\">\n                            <span>₹</span><span>{{ current.price }}</span>\n                        </div>\n                        <div>\n                            \"Lorem ipsum dolor sit hjbsdc jsnznxk\n                            amet,consectetur adipiscing elit, sed do eiusmod\n                            tempor incididunt ut labore et dolore magna aliqua.\n                            Ut enim ad minim veniam, quis nostru exercitation.\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >illo inventore veritatis et quasi architecto\n                                beatae vitae</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >quis nostrum exercitationem ullam corporis\n                                suscipit laboriosam, nisi ut aliquid ex ea\n                                commodi consequatur? Quis autem vel</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >Duis aute irure dolor in reprehenderit in\n                                voluptate velit esse cillum dolore eu\n                            </span>\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >eius modi tempora incidunt ut labore et dolore\n                                magnam aliquam quaerat voluptatem.</span\n                            >\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div class=\"actions\">\n                <button :disabled=\"clicked\" @click=\"cancel($event)\">\n                    Cancel\n                </button>\n                <button v-if=\"currentdetails?.razorpay_success?.short_url\">\n                    <a\n                        target=\"_blank\"\n                        :href=\"currentdetails?.razorpay_success?.short_url\"\n                        >Activate</a\n                    >\n                </button>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    props: [\"host\", \"plans\", \"current\", \"currentdetails\"],\n    components: {\n        flashMessage,\n    },\n    data() {\n        return {\n            clicked: false,\n            message: null,\n        };\n    },\n    methods: {\n        cancel() {\n            let _this = this;\n            this.clicked = true;\n            axios\n                .post(route(\"cancelSubscription\", _this.currentdetails?.sub_id))\n                .then((res) => {\n                    _this.message = res.data.message;\n                    setTimeout(function () {\n                        _this.message = null;\n                        _this.clicked = false;\n                        window.location.reload();\n                    }, 3000);\n                })\n                .catch((err) => {\n                    _this.message = err.response?.data?.message;\n                    setTimeout(function () {\n                        _this.message = null;\n                        _this.clicked = false;\n                        //window.location.reload();\n                    }, 2000);\n                });\n        },\n    },\n};\n</script>\n\n<style scoped>\n#section-8 > div:nth-child(1)::-webkit-scrollbar {\n    display: none;\n}\n\n/* Hide scrollbar for IE, Edge and Firefox */\n#section-8 > div:nth-child(1) {\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n}\n/* price card css start */\n#section-8 {\n    background: #f8f8f8;\n    padding: 3em 0;\n}\n#section-8 > div {\n    display: flex;\n}\n.price-card-container {\n    padding-left: 19px;\n    width: max-content;\n    align-self: center;\n    position: relative;\n}\n.price-card-container > div:nth-child(1) {\n    padding-bottom: 20px;\n    position: absolute;\n}\n.price-card-container > div:nth-child(1)::before {\n    content: \"\";\n    display: block;\n    clip-path: polygon(0 62%, 28% 62%, 28% 100%, 28% 100%);\n    width: 65px;\n    height: 50px;\n    background: #04262e;\n    z-index: 99999;\n    top: 26px;\n    position: absolute;\n    margin-left: -17px;\n}\n.plan {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 18.4072px;\n    line-height: 18px;\n    color: #ffffff;\n    /* background: linear-gradient(90.27deg, #9b3154 1.62%, #6d223b 95.36%); */\n    width: 111px;\n    height: 37px;\n    clip-path: polygon(0 0, 69% 0, 100% 100%, 0 100%);\n    margin-left: -17px;\n    position: absolute;\n    top: 20px;\n}\n.details-cont {\n    background: #ffffff;\n    border-radius: 20px;\n    padding: 1.5em 2em;\n    padding-top: 5em;\n    width: 266px;\n}\n.details-cont > div:nth-child(1) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 11.1324px;\n    line-height: 13px;\n    color: #464646;\n    text-align: right;\n    padding-right: 6em;\n}\n.details-cont > div:nth-child(2) > span:nth-child(1) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 33.6722px;\n    line-height: 41px;\n    color: #000000;\n    padding-right: 10px;\n}\n\n.details-cont > div:nth-child(2) > span:nth-child(2) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 49.4522px;\n    line-height: 60px;\n    color: #000000;\n}\n.details-cont > div:nth-child(3) {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 12px;\n    line-height: 12px;\n    color: #000000;\n    margin-top: 1em;\n    margin-bottom: 1.5em;\n}\n.pros {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 9px;\n    line-height: 9px;\n    color: #5e5e5e;\n    display: flex;\n    margin-bottom: 10px;\n}\n.pros > img {\n    margin-right: 12px;\n    width: 16px;\n    height: 16px;\n}\n.pros > span {\n    vertical-align: middle;\n}\n.buy {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 11.3478px;\n    line-height: 11px;\n    color: #ffffff;\n    border: none;\n    outline: 0;\n    background: linear-gradient(90deg, #9b3154 0%, #6d223b 100%);\n    border-radius: 20px;\n    padding: 12px 28px;\n}\n/* price card css end */\n.info {\n    display: block !important;\n}\n.info > div {\n    display: block;\n    width: 100%;\n}\n</style>\n"],"sourceRoot":""}]);
